<template>
  <div>
    <breadcrumb v-bind:title="title"></breadcrumb>



    <div class="row" v-bind:class="[{loaderShow:!loading ,loaderHidden:loading }]">
      <div class="col-sm-12">
        <table class="table table-bordered">
          <tr>
            <th>عنوان</th>
            <th>عملیات</th>
          </tr>
          <tbody>
          <tr v-for="type in types">
            <td>{{type.label}}</td>
            <td>
              <div class="op-icon">
                <a @click.prevent="deleteType(type.value)"><i class="fa fa-remove deleteIcon"></i></a>
              </div>
            </td>
          </tr>
          </tbody>
        </table>
      </div>
    </div>


    <div v-bind:class="[{loaderShow:loading ,loaderHidden:!loading }]">
      <Spinner fadeIn="quarter" class="spinnerClass" name="three-bounce" color="#57abfc"/>
    </div>




  </div>
</template>

<script>
  import Breadcrumb from '../../components/Breadcrumb'
  import Notify from '../../services/notifiction/Notification'
  import TypesApi from '../../services/api/type/TypeApi'
  const typesApi = new TypesApi()


  export default {
    name: 'ListTypes',
    components: { Breadcrumb },
    data () {
      return {
        title: ' لیست تایپ ها',
        types: {},
        loading: false,
        pageStatus: 'list'
      }
    },
    methods: {

      disableLoading(){
        this.loading = false;
      },
      showLoading(){
        this.loading = true;
      },

      getTypes () {

        this.showLoading();
        typesApi.getTypes().then((response) => {

          this.types = response.data.data
          this.disableLoading();

        })
      },

      deleteType (id) {

        this.showLoading();

        typesApi.deleteType(id).then((response) => {
          this.disableLoading()
          switch (response.data.status_code) {
            case 200:
              Notify.success(response.data.message)
              this.getTypes();
              break

            case 422://validation error
              Notify.error(response.data.message)
              break

            case 500:
              Notify.error("internal server error")

          }

        })

      },
    },
    computed:{


    },

    created () {
      this.getTypes()
    }
  }
</script>
