<template>
  <div>
    <breadcrumb v-bind:title="title"></breadcrumb>


    <div v-bind:class="[{loaderShow:loading ,loaderHidden:!loading }]">
      <Spinner fadeIn="quarter" class="spinnerClass" name="three-bounce" color="#57abfc"/>
    </div>


    <div v-bind:class="[{loaderShow:!loading ,loaderHidden:loading }]">


      <div class="row">
        <div class="col-sm-3">
          <div class="form-group">
            <label>فیلتر بر اساس درس</label>
            <v-select v-model="lessonIdFilter" :options="lessons" return-object></v-select>
          </div>
        </div>
      </div>


      <div class="row">
        <div class="col-sm-12">
          <table class="table table-bordered" style="overflow-x: scroll">
            <tr>
              <th>نام</th>
              <th>درس مربوطه</th>
              <th>نوع</th>
              <th>عملیات</th>
            </tr>
            <tbody>
            <tr v-for="text in texts">
              <td>{{text.title}}</td>
              <td>{{text.lesson.title}}</td>
              <td>{{text.type.title}}</td>
              <td>
                <div class="op-icon">
                  <router-link :to="{name:'EditText',params:{textid:text._id}}"><i class="fa fa-edit"></i>
                  </router-link>
                  <a @click.prevent="deleteText(text._id)"><i class="fa fa-remove deleteIcon"></i></a>
                </div>
              </td>
            </tr>
            </tbody>
          </table>
        </div>
      </div>


      <div class="row">
        <div class="col-sm-6">

          <template>
            <paginate
              :page-count="totalPage"
              :page-range="3"
              :margin-pages="2"
              :click-handler="clickCallback"
              :prev-text="'Prev'"
              :next-text="'Next'"
              :container-class="'pagination'"
              :page-class="'page-item'">
            </paginate>
          </template>

        </div>
      </div>
    </div>


  </div>
</template>

<script>
  import Breadcrumb from '../../components/Breadcrumb'
  import Notify from '../../services/notifiction/Notification'
  import TextApi from '../../services/api/text/TextApi'
  import LessonApi from '../../services/api/lesson/LessonApi'

  const textApi = new TextApi()
  const lessonApi = new LessonApi()

  export default {
    name: 'ListTexts',
    components: { Breadcrumb },
    data () {
      return {
        title: 'لیست متن ها',
        texts: {},
        lessons: [],
        pageStatus: 'list',
        loading: false,
        page: 1,
        limit: 5,
        totalPage: 1,
        lessonIdFilter: ''
      }
    },
    methods: {

      clickCallback (pageNum) {
        this.page = pageNum
        this.texts = {}
        this.getTexts()
      },

      getTexts () {
        this.showLoading

        textApi.getTextsByPaginate(this.page, this.limit).then((response) => {
          this.disableLoading
          if (response.data.status_code == 200) {
            this.texts = response.data.data
            this.totalPage = response.data.totalPage
          }

        })
      },

      getTextsByPaginateAndFilter (lsnid) {
        this.showLoading

        textApi.getTextsByPaginateAndFilter(this.page, this.limit, lsnid).then((response) => {
          this.disableLoading
          if (response.data.status_code == 200) {
            this.texts = response.data.data
            this.totalPage = response.data.totalPage
          }

        })
      },

      deleteText (id) {

        textApi.deleteText(id).then((response) => {

          if (response.data.status_code == 200) {

            Notify.success(response.data.message)
            this.getTexts()

          }

        })

      },

      getLessonsSelective () {

        lessonApi.getLessonsSelective().then((response) => {

          this.lessons = response.data.data

        })

      },

    },
    computed: {

      disableLoading () {
        this.loading = false
      },
      showLoading () {
        this.loading = true
      }
    },
    created () {
      this.getTexts()
      this.getLessonsSelective()
    },
    watch: {

      'lessonIdFilter': function () {
        const lsnId = this.lessonIdFilter;

        if (!lsnId) {

          this.getTexts()

        } else {
          this.getTextsByPaginateAndFilter(this.lessonIdFilter.value)

        }

      }

    }
  }
</script>

