<template>
  <div>
    <breadcrumb v-bind:title="title" v-bind:pageStatus="pageStatus"></breadcrumb>

    <form>

      <div class="row">

        <div class="col-sm-4" style="margin-top: 30px;">

          <div class="col-sm-6">

            <div class="form-group">
              <span style="padding-left: 10px;">کوییز درس</span>
              <input type="radio" id="quiz" value="quiz" v-model="question.type" v-on:change="onQuizRadioChange">
            </div>

          </div>
          <div class="col-sm-6">

            <div class="form-group">
              <span style="padding-left: 10px;">آزمون دوره ای</span>
              <input type="radio" id="exame" value="exam" v-model="question.type" v-on:change="onExamRadioChange">
            </div>

          </div>

        </div>


        <div class="col-sm-4">

          <div class="form-group" ref="lessonSelectDiv">
            <label>انتخاب درس مربوطه</label>
            <v-select v-if="pageStatus === 'add' " v-model="question.lesson" :options="lessons"></v-select>
            <v-select v-else :value="selectedLesson" v-model="question.lesson" :options="lessons"></v-select>
          </div>

        </div>


        <div class="col-sm-4">

          <div class="form-group" ref="quizSelectDiv">
            <label>انتخاب کوییز مربوطه</label>
            <v-select id="examsSelect" v-if="pageStatus === 'add' " v-model="question.exam" :options="exams"></v-select>
            <v-select v-else :value="selectedExam" v-model="question.exam" :options="exams"></v-select>
          </div>

        </div>


      </div>


      <div class="row" style="margin-top: 20px;">
        <div class="col-sm-12">
          <div class="inner_options_block">
            <div class="form-group">
              <label>امتیاز سوال</label>
              <input required placeholder="مقدار را به صورت عدد وارد کنید" class="form-control" type="text"
                     v-model="question.score">
            </div>
          </div>

        </div>
      </div>

      <div class="row">
        <div class="col-sm-12">

          <div class="inner_options_block">
            <div class="form-group">
              <label>صورت سوال</label>
              <input required class="form-control" type="text" v-model="question.content">
            </div>
          </div>

        </div>
      </div>


      <div class="row" style="margin-top: 20px">
        <div class="col-sm-6">

          <div class="inner_options_block">

            <div class="form-group">
              <label>گزینه 1</label>
              <input required class="form-control" type="text" v-model="question.answers[0].content">
            </div>

            <div class="form-group">
              <span>توضیحات گزینه یک</span>
              <input placeholder="در صوررتی که توضیح ندارد فیلد را خالی بگذارید" class="form-control" type="text"
                     v-model="question.answers[0].description">

            </div>

            <div class="form-group">
              <label style="margin-left:10px;">جواب صحیح</label>
              <input type="radio" value="0" v-model="question.trueIndex">
            </div>

          </div>

        </div>
        <div class="col-sm-6">
          <div class="inner_options_block">
            <div class="form-group">
              <label>گزینه 2</label>
              <input required class="form-control" type="text" v-model="question.answers[1].content">
            </div>

            <div class="form-group">
              <div class="form-group">
                <span>توضیحات گزینه دو</span>
                <input placeholder="در صوررتی که توضیح ندارد فیلد را خالی بگذارید" class="form-control" type="text"
                       v-model="question.answers[1].description">
              </div>

            </div>

            <div class="form-group">
              <label style="margin-left:10px;">جواب صحیح</label>
              <input type="radio" value="1" v-model="question.trueIndex">
            </div>

          </div>

        </div>
      </div>

      <div class="row" style="margin-top: 20px">
        <div class="col-sm-6">

          <div class="inner_options_block">

            <div class="form-group">
              <label>گزینه 3</label>
              <input required class="form-control" type="text" v-model="question.answers[2].content">
            </div>

            <div class="form-group">
              <span>توضیحات گزینه سه</span>
              <input placeholder="در صوررتی که توضیح ندارد فیلد را خالی بگذارید" class="form-control" type="text"
                     v-model="question.answers[2].description">
            </div>

            <div class="form-group">
              <label style="margin-left:10px;">جواب صحیح</label>
              <input type="radio" value="2" v-model="question.trueIndex">
            </div>

          </div>

        </div>
        <div class="col-sm-6">

          <div class="inner_options_block">

            <div class="form-group">
              <label>گزینه 4</label>
              <input required class="form-control" type="text" v-model="question.answers[3].content">
            </div>

            <div class="form-group">
              <div class="form-group">
                <span>توضیحات گزینه چهار</span>
                <input placeholder="در صوررتی که توضیح ندارد فیلد را خالی بگذارید" class="form-control" type="text"
                       v-model="question.answers[3].description">
              </div>

            </div>

            <div class="form-group">
              <label style="margin-left:10px;">جواب صحیح</label>
              <input type="radio" value="3" v-model="question.trueIndex">
            </div>
          </div>
        </div>
      </div>


      <button v-if="pageStatus === 'add' " class="btn btn-success" @click.prevent="storeQuestion">افزودن</button>
      <button v-else class="btn btn-success" @click.prevent="updateQuestion(question._id)">ویرایش</button>

    </form>

  </div>
</template>

<script>
  import Breadcrumb from '../../components/Breadcrumb'
  import Notify from '../../services/notifiction/Notification'
  import ValidationErrors from '../../ValidationErrors'
  import LessonApi from '../../services/api/lesson/LessonApi'
  import QuestionApi from '../../services/api/question/QuestionApi'
  import ExamApi from '../../services/api/exam/ExamApi'

  const lessonApi = new LessonApi()
  const questionApi = new QuestionApi()
  const examApi = new ExamApi()

  export default {
    name: 'AddQuestion',
    components: { Breadcrumb },
    data () {
      return {

        title: ' سوال',
        selectedExam: {},
        selectedLesson: {},

        question: {
          answers: [

            {
              content: '',
              description: '',
            },
            {
              content: '',
              description: '',
            },
            {
              content: '',
              description: '',
            },
            {
              content: '',
              description: '',
            },

          ],

        },
        showProgressBar: false,
        lessons: [],
        exams: [],
        pageStatus: 'add',
        ValidationErrors: new ValidationErrors(),
      }
    },
    methods: {

      storeQuestion () {

        this.showLoading()
        questionApi.create(this.question).then((response) => {
          this.disableLoading()

          this.question = {}

          switch (response.data.status_code) {

            case 201:
              Notify.success(response.data.message)
              break

            case 422://validation error
              this.ValidationErrors.store(response.data.errors)
              Notify.error(response.data.message)
              break

            case 500:
              Notify.error('internal server error')
              break

          }
        })

      },
      updateQuestion (id) {

        this.showLoading()
        questionApi.updateQuestion(id, this.question).then((response) => {
          this.disableLoading()

          this.question = {}

          switch (response.data.status_code) {

            case 201:
              Notify.success(response.data.message)
              break

            case 200:
              Notify.success(response.data.message)
              break

            case 422://validation error
              this.ValidationErrors.store(response.data.errors)
              Notify.error(response.data.message)
              break

            case 500:
              Notify.error('internal server error')
              break

          }
        })

      },
      getQuestion (id) {

        questionApi.getQuestion(id).then((response) => {

          this.question = response.data.data

          this.selectedLesson = {

            label: response.data.data.lesson.label,
            value: response.data.data.lesson.value,

          }

          this.selectedExam = {

            label: response.data.data.exam.label,
            value: response.data.data.exam.value,

          }

        })

      },
      onQuizRadioChange () {

        this.$refs.quizSelectDiv.setAttribute('class', 'disableClass')
        this.$refs.lessonSelectDiv.classList.remove('disableClass')

      },
      onExamRadioChange () {

        this.$refs.lessonSelectDiv.setAttribute('class', 'disableClass')
        this.$refs.quizSelectDiv.classList.remove('disableClass')

      },
      getLessonsSelective () {

        lessonApi.getLessonsSelective().then((response) => {

          this.lessons = response.data.data

        })

      },
      getExamsSelective () {

        examApi.getExamsSelective().then((response) => {

          this.exams = response.data.data

        })

      },
      disableLoading () {
        this.loading = false
      },
      showLoading () {
        this.loading = true
      }

    },
    computed: {},
    created: function () {
      if (this.$route.params.questionid) {
        this.pageStatus = 'edit'
        this.getQuestion(this.$route.params.questionid)
      }

      this.getLessonsSelective()
      this.getExamsSelective()

    },
  }
</script>

<style scoped>
  label {
    color: #204d74;
  }

  .disableClass {

    pointer-events: none;
    opacity: 0.4;
  }

</style>
