import axios from 'axios'

const API_URL = 'http://185.94.99.69:8080/api/trick/'

export default class TrickApi{

  create (formData) {

    return axios.post(API_URL,
      formData,
      {

        headers: { 'Authorization': 'Bearer ' + localStorage.getItem('jwt'), 'content-type': 'multipart/form-data' },
        onUploadProgress: (progressEvent) => {

          this.uploadPercentage  = parseInt(Math.round((progressEvent.loaded * 100) / progressEvent.total))
          console.log( this.uploadPercentage);


        }

      },
    )
  }

  getTrick (id) {
    return axios.get(API_URL + id, {
      headers: { 'Authorization': 'Bearer ' + localStorage.getItem('jwt') }
    })
  }

  updateTrick (id, formdata) {
    return axios.put(API_URL + id, formdata, {
      headers: { 'Authorization': 'Bearer ' + localStorage.getItem('jwt') , 'content-type': 'multipart/form-data' }
    })

  }

  getTricks () {

    return axios.get(API_URL,
      {
        headers: { 'Authorization': 'Bearer ' + localStorage.getItem('jwt') }
      }
    )
  }

  deleteTrick (id) {
    return axios.delete(API_URL + id,
      {
        headers: { 'Authorization': 'Bearer ' + localStorage.getItem('jwt') }
      }
    )
  }


  getTricksByPaginate (page,limit) {

    return axios.get(API_URL + '?page=' + page + '&limit=' + limit,
      {
        headers: { 'Authorization': 'Bearer ' + localStorage.getItem('jwt') }
      }
    );
  }


  getTricksByPaginateAndFilter (page,limit,lsnId) {

    return axios.get(API_URL + '?page=' + page + '&limit=' + limit + '&lsnId=' + lsnId ,
      {
        headers: { 'Authorization': 'Bearer ' + localStorage.getItem('jwt') }
      }
    );
  }



}
