<template>
  <div>
    <breadcrumb v-bind:title="title"></breadcrumb>


    <div v-bind:class="[{loaderShow:loading ,loaderHidden:!loading }]">
      <Spinner fadeIn="quarter" class="spinnerClass" name="three-bounce" color="#57abfc"/>
    </div>

    <div class="row" v-bind:class="[{loaderShow:!loading ,loaderHidden:loading }]">
      <div class="col-sm-12">
        <table class="table table-bordered">
          <tr>
            <th>نام سطح</th>
            <th>توضیحات سطح</th>
            <th>ترتیب</th>
            <th>کاور</th>
            <th>عملیات</th>
          </tr>
          <tbody>
          <tr v-for="level in levels">
            <td>{{level.title}}</td>
            <td>{{level.description}}</td>
            <td>{{level.order}}</td>
            <td>
              <img class="thumb" v-bind:src="level.avatarUrl">
            </td>
            <td>
              <div class="op-icon">
                <router-link :to="{name:'EditLevel',params:{lvlid:level._id}}"><i class="fa fa-edit"></i></router-link>
                <a @click.prevent="deleteLevel(level._id)"><i class="fa fa-remove deleteIcon"></i></a>
              </div>
            </td>
          </tr>
          </tbody>
        </table>
      </div>
    </div>
  </div>
</template>

<script>
  import Breadcrumb from '../../components/Breadcrumb'
  import Notify from '../../services/notifiction/Notification'
  import LevelApi from '../../services/api/level/LevelApi'
  const levelApi = new LevelApi()

  export default {
    name: 'ListLevels',
    components: { Breadcrumb },
    data () {
      return {
        title: 'لیست سطح ها',
        levels: {},
        loading:false,
        pageStatus: 'list'
      }
    },
    methods: {

      getLevels () {
        this.showLoading;
        levelApi.getLevels().then((response) => {
          this.disableLoading;
          this.levels = response.data.data

        })
      },

      deleteLevel (id) {
        levelApi.deleteLevel(id).then((response) => {
          switch (response.data.status_code) {

            case 200:
              Notify.success(response.data.message)
              this.getLevels();
              break

            case 422://validation error
              Notify.error(response.data.message)
              break

            case 500:
              Notify.error("internal server error")

          }

        })

      },

    },
    computed:{
      disableLoading(){
        this.loading = false;
      },
      showLoading(){
        this.loading = true;
      }

    },

    created () {
      this.getLevels()
    }
  }
</script>
