import axios from 'axios'

const API_URL = 'http://185.94.99.69:8080/api/lesson/type/'
const TYPES_SELECTIVE = "http://185.94.99.69:8080/api/lesson/type"

export default class TypeApi {

  create (type) {
    return axios.post(API_URL,
      type,
      {
        headers: { 'Authorization': 'Bearer ' + localStorage.getItem('jwt') }
      })
  }

  getType (id) {
    return axios.get(API_URL + id, {
      headers: { 'Authorization': 'Bearer ' + localStorage.getItem('jwt') }
    });
  }



  updateType (id,type) {
    return axios.put(API_URL + id, type, {
      headers: { 'Authorization': 'Bearer ' + localStorage.getItem('jwt') }
    });

  }

  getTypes(){

    return axios.get(API_URL,
      {
        headers: {"Authorization":'Bearer ' + localStorage.getItem('jwt')}
      }
    );
  }


  deleteType(id)
  {
    return axios.delete(API_URL + id,
      {
        headers: {"Authorization":'Bearer ' + localStorage.getItem('jwt')}
      }
    );
  }


  getTypesSelective () {

    return axios.get(TYPES_SELECTIVE,
      {
        headers: { 'Authorization': 'Bearer ' + localStorage.getItem('jwt') }
      }
    );

  }


}
