<template>
  <div>
    <breadcrumb v-bind:title="title"></breadcrumb>


    <div v-bind:class="[{loaderShow:loading ,loaderHidden:!loading }]">
      <Spinner fadeIn="quarter" class="spinnerClass" name="three-bounce" color="#57abfc"/>
    </div>

    <div class="row" v-bind:class="[{loaderShow:!loading ,loaderHidden:loading }]">
      <div class="col-sm-12">
        <table class="table table-bordered">
          <tr>
            <th>نام</th>
            <th>توضیحات</th>
            <th>لینک</th>
            <th>کاور</th>
            <th>عملیات</th>
          </tr>
          <tbody>
          <tr v-for="notification in notifications">
            <td>{{notification.title}}</td>
            <td>{{notification.text}}</td>
            <td>{{notification.link}}</td>
            <td>
              <img class="thumb" v-bind:src="notification.avatarUrl">
            </td>
            <td>
              <div class="op-icon">
                <router-link :to="{name:'EditNotification',params:{notificationid:notification._id}}"><i class="fa fa-edit"></i></router-link>
                <a @click.prevent="deleteNotification(notification._id)"><i class="fa fa-remove deleteIcon"></i></a>
              </div>
            </td>
          </tr>
          </tbody>
        </table>
      </div>
    </div>
  </div>
</template>

<script>
  import Breadcrumb from '../../components/Breadcrumb'
  import Notify from '../../services/notifiction/Notification'
  import NotificationApi from '../../services/api/notification/NotificationApi'
  const notificationApi = new NotificationApi()

  export default {
    name: 'ListNotifications',
    components: { Breadcrumb },
    data () {
      return {
        title: 'لیست نوتیفیکیشن ها',
        notifications: {},
        loading:false,
        pageStatus: 'list'
      }
    },
    methods: {

      getNotifications () {
        this.showLoading();
        notificationApi.getNotifications().then((response) => {
          this.disableLoading();
          this.notifications = response.data.data

        })
      },

      deleteNotification (id) {
        notificationApi.deleteNotification(id).then((response) => {
          switch (response.data.status_code) {

            case 200:
              Notify.success(response.data.message)
              this.getNotifications();
              break

            case 422://validation error
              Notify.error(response.data.message)
              break

            case 500:
              Notify.error("internal server error")

          }

        })

      },
      disableLoading(){
        this.loading = false;
      },
      showLoading(){
        this.loading = true;
      }


    },
    computed:{

    },

    created () {
      this.getNotifications()
    }
  }
</script>
