<template>
  <div>
    <breadcrumb v-bind:title="title" v-bind:pageStatus="pageStatus"></breadcrumb>

    <form enctype="multipart/form-data">
      <div class="row">
        <div class="col-sm-4">


          <div class="form-group">
            <label>فایل (ویدیو) </label>
            <input type="file" id="file" ref="myFiles" class="custom-file-input"
                   @change="previewFiles">
          </div>
          <span class="input_alert">{{showValidationTitle('file')}}</span>


          <div class="form-group">
            <label>فایل زیرنویس ( در صورت وجود )  </label>
            <input type="file" id="srt" ref="mySrtFiles" class="custom-file-input"
                   @change="previewSrtFiles">
          </div>
          <span class="input_alert">{{showValidationTitle('file')}}</span>


          <div class="form-group">
            <label>عنوان</label>
            <input class="form-control" type="text" v-model="trick.title">
          </div>
          <span class="input_alert">{{showValidationTitle('title')}}</span>


          <div class="form-group">
            <label>ترتیب</label>
            <select class="form-control" v-model="trick.order">

              <option value="1">اول</option>
              <option value="2">دوم</option>
              <option value="3">سوم</option>
              <option value="4">چهارم</option>

            </select>
          </div>
          <span class="input_alert">{{showValidationTitle('order')}}</span>



        </div>

        <div class="col-sm-8">
          <div class="form-group">
            <label>متن</label>
            <textarea v-if="pageStatus === 'add' " class="form-control" rows="16" cols="30"
                      v-model="trick.text"></textarea>
            <textarea v-else class="form-control" rows="16" cols="30" v-model="trick.text"></textarea>
          </div>
        </div>
        <span class="input_alert">{{showValidationTitle('text')}}</span>


      </div>

      <button v-if="pageStatus === 'add' " class="btn btn-success" @click.prevent="storeTrick">افزودن</button>
      <button v-else class="btn btn-success" @click.prevent="updateTrick(trick._id)">ویرایش</button>

    </form>

    <div class="row" style="margin-top: 20px;"
         v-bind:class="[{loaderShow:showProgressBar ,loaderHidden:!showProgressBar }]">
      <div class="col-sm-12">

        <progress-bar v-bind:val="getPercentage" max="100" :text="uploadPercentageWithPercent"></progress-bar>

      </div>
    </div>


  </div>

</template>

<script>
  import Breadcrumb from '../../components/Breadcrumb'
  import Notify from '../../services/notifiction/Notification'
  import ValidationErrors from '../../ValidationErrors'

  import TrickApi from '../../services/api/trick/TrickApi'

  const trickApi = new TrickApi()

  export default {
    name: 'AddTrick',
    components: { Breadcrumb },
    data () {
      return {
        title: ' تیریک',
        trick: {},
        files: {},
        srtFiles: {},
        showProgressBar: false,
        pageStatus: 'add',
        uploadPercentage: 0,
        ValidationErrors: new ValidationErrors(),
      }
    },
    methods: {
      updateTrick (id) {
        this.showingProgressBar()
        var formData = new FormData()

        formData.append('title', this.trick.title)
        formData.append('order', this.trick.order)
        formData.append('text', this.trick.text)
        formData.append('file', this.files)
        formData.append('srt', this.srtFiles)


        this.$http.put('http://185.94.99.69:8080/api/trick/' + id, formData, {

          headers: { 'Authorization': 'Bearer ' + localStorage.getItem('jwt'), 'content-type': 'multipart/form-data' },
          onUploadProgress: function (progressEvent) {
            this.uploadPercentage = parseInt(Math.round((progressEvent.loaded * 100) / progressEvent.total))
          }.bind(this)

        }).then((response) => {
          this.hiddenProgressBar()
          this.resetPercentage()

          switch (response.data.status_code) {

            case 201:
              Notify.success(response.data.message)
              this.resetPercentage()
              this.trick = {}
              break

            case 200:
              Notify.success(response.data.message)
              this.resetPercentage()
              this.trick = {}
              break

            case 422://validation error
              this.ValidationErrors.store(response.data.errors)
              Notify.error(response.data.message)
              break

            case 500:
              Notify.error(response.data.message)
              break

          }

        })

      },
      storeTrick () {
        this.showingProgressBar()
        var formData = new FormData()

        formData.append('title', this.trick.title)
        formData.append('order', this.trick.order)
        formData.append('text', this.trick.text)
        formData.append('file', this.files)
        formData.append('srt', this.srtFiles)


        this.$http.post('http://185.94.99.69:8080/api/trick/', formData, {

          headers: { 'Authorization': 'Bearer ' + localStorage.getItem('jwt'), 'content-type': 'multipart/form-data' },
          onUploadProgress: function (progressEvent) {
            this.uploadPercentage = parseInt(Math.round((progressEvent.loaded * 100) / progressEvent.total))
          }.bind(this)

        }).then((response) => {

          this.hiddenProgressBar()
          this.resetPercentage()
          switch (response.data.status_code) {

            case 201:
              Notify.success(response.data.message)
              this.trick = {}
              break

            case 200:
              Notify.success(response.data.message)
              this.trick = {}
              break

            case 422://validation error
              this.ValidationErrors.store(response.data.errors)
              Notify.error(response.data.message)
              break

            case 500:
              Notify.error(response.data.message)
              break

          }

        })

      },
      getTrick (id) {

        trickApi.getTrick(id).then((response) => {
          this.trick = response.data.data
        })

      },

      showValidationTitle (item) {
        return this.ValidationErrors.errorTitle(item)
      },

      previewFiles () {
        this.files = this.$refs.myFiles.files[0]
      },

      previewSrtFiles () {
        this.srtFiles = this.$refs.mySrtFiles.files[0]
      },
      hiddenProgressBar () {
        this.showProgressBar = false
      },
      showingProgressBar () {
        this.showProgressBar = true
      },
      resetPercentage () {
        this.uploadPercentage = 0
      },

    },
    computed: {

      uploadPercentageWithPercent () {
        return this.uploadPercentage.toString().concat('%')
      },
      getPercentage () {
        return this.uploadPercentage
      },


    },
    created: function () {
      if (this.$route.params.trickid) {
        this.pageStatus = 'edit'
        this.getTrick(this.$route.params.trickid)
      }
    },

  }
</script>

