<template>
  <div>
    <breadcrumb v-bind:title="title" v-bind:pageStatus="pageStatus"></breadcrumb>
    <form style="width: 50%">
      <div class="form-group">
        <label>نام سطح</label>
        <input type="text" name="title" class="form-control" v-model="level.title"/>

        <span class="input_alert">{{showValidationTitle('title')}}</span>
      </div>

      <div class="form-group">
        <label>توضیحات سطح</label>

        <input type="text" name="description" class="form-control" v-model="level.description"/>
        <span class="input_alert">{{showValidationTitle('description')}}</span>
      </div>

      <div class="form-group">
        <label>ترتیب سطح</label>
        <select class="form-control" v-model="level.order">

          <option value="1">اول</option>
          <option value="2">دوم</option>
          <option value="3">سوم</option>
          <option value="4">چهارم</option>

        </select>
        <span class="input_alert">{{showValidationTitle('order')}}</span>

      </div>


      <div class="form-group">
        <label>آپلود عکس </label>
        <input type="file" id="file" ref="myFiles" class="custom-file-input"
               @change="previewFiles">
      </div>
      <span class="input_alert">{{showValidationTitle('file')}}</span>




      <button v-if="pageStatus === 'add' " class="btn btn-success" @click.prevent="storeLevel">افزودن</button>
      <button v-else class="btn btn-success" @click.prevent="updateLevel(level._id)">ویرایش</button>
    </form>


    <div class="row" style="margin-top: 20px;" v-bind:class="[{loaderShow:showProgressBar ,loaderHidden:!showProgressBar }]">
      <div class="col-sm-12">

        <progress-bar v-bind:val="getPercentage" max="100" :text="uploadPercentageWithPercent"></progress-bar>

      </div>
    </div>



  </div>
</template>

<script>
  import Breadcrumb from '../../components/Breadcrumb'
  import Notify from '../../services/notifiction/Notification'
  import ValidationErrors from '../../ValidationErrors'
  import LevelApi from '../../services/api/level/LevelApi'

  const levelApi = new LevelApi()

  export default {
    name: 'AddLevel',
    components: { Breadcrumb },
    data () {
      return {
        title: ' سطح',
        level: {},
        showProgressBar:false,
        files: {},
        pageStatus: 'add',
        uploadPercentage: 0,
        ValidationErrors: new ValidationErrors(),
      }
    },
    methods: {
      storeLevel () {

        this.showingProgressBar();

        var formData = new FormData()

        formData.append('title', this.level.title)
        formData.append('description', this.level.description)
        formData.append('order', this.level.order)
        formData.append('file', this.files)


        this.$http.post('http://185.94.99.69:8080/api/level/', formData, {

          headers: { 'Authorization': 'Bearer ' + localStorage.getItem('jwt'), 'content-type': 'multipart/form-data' },
          onUploadProgress: function (progressEvent) {
            this.uploadPercentage = parseInt(Math.round((progressEvent.loaded * 100) / progressEvent.total))
          }.bind(this)

        }).then((response) => {
          this.hiddenProgressBar()
          this.resetPercentage()

          switch (response.data.status_code) {

            case 201:
              Notify.success(response.data.message)
              this.level = {}
              break

            case 200:
              Notify.success(response.data.message)
              this.level = {}
              break

            case 422://validation error
              this.ValidationErrors.store(response.data.errors)
              Notify.error(response.data.message)
              break

            case 500:
              Notify.error(response.data.message)
              break

          }

        })
      },

      getLevel (id) {

        levelApi.getLevel(id).then((response) => {

          this.level = response.data.data

        });

      },


      updateLevel (id) {

        this.showingProgressBar();
        var formData = new FormData()
        formData.append('title', this.level.title)
        formData.append('description', this.level.description)
        formData.append('order', this.level.order)
        formData.append('file', this.files)

        this.$http.put('http://185.94.99.69:8080/api/level/'+ id, formData, {

          headers: { 'Authorization': 'Bearer ' + localStorage.getItem('jwt'), 'content-type': 'multipart/form-data' },
          onUploadProgress: function (progressEvent) {
            this.uploadPercentage = parseInt(Math.round((progressEvent.loaded * 100) / progressEvent.total))
          }.bind(this)

        }).then((response) => {

          this.hiddenProgressBar()
          switch (response.data.status_code) {

            case 201:
              Notify.success(response.data.message)
              this.level = {}
              break

            case 200:
              Notify.success(response.data.message)
              this.level = {}
              break

            case 422://validation error
              this.ValidationErrors.store(response.data.errors)
              Notify.error(response.data.message)
              break

            case 500:
              Notify.error(response.data.message)
              break

          }
          this.resetPercentage();


        })



      },

      previewFiles () {
        this.files = this.$refs.myFiles.files[0]
      },

      hiddenProgressBar () {
        this.showProgressBar = false
      },
      showingProgressBar () {
        this.showProgressBar = true
      },

      showValidationTitle (item) {
        return this.ValidationErrors.errorTitle(item)
      },
      resetPercentage(){
        this.uploadPercentage = 0;
      }
    },
    computed: {

      uploadPercentageWithPercent(){

        return this.uploadPercentage.toString().concat('%');
      },
      getPercentage(){
        return this.uploadPercentage;
      },



    },
    created () {

      if (this.$route.params.lvlid) {
        this.pageStatus = 'edit'
        this.getLevel(this.$route.params.lvlid)
      }

    }
  }
</script>

