<template>

  <div>

    <div class="card fadeInDown" id="loginBox" >
      <div class="card-header">
        <h5 class="text-center">مدیریت اکادمی زبان</h5>
        <h6 class="text-center">لطفا نام کاربری و کلمه عبور خود را وارد کنید</h6>
        <div class="card-body">
          <form>
            <div class="form-group">
              <label>نام کاربری</label>
              <input name="username" type="text" class="form-control" v-model="adminData.username"/>
            </div>
            <div class="form-group">
              <label>کلمه عبور</label>
              <input name="password" type="password" class="form-control" v-model="adminData.password"/>
            </div>

            <button type="submit" class="btn btn-primary btnLogin" @click.prevent="doLogin">ورود</button>

          </form>

          <div v-bind:class="[{loaderShow:loading ,loaderHidden:!loading }]" style="margin-top: 20px;">
            <Spinner fadeIn="quarter" class="spinnerClass" name="three-bounce" color="#57abfc"/>
          </div>


        </div>
      </div>
    </div>

  </div>

</template>

<script>

  export default {
    name: "Login",
    data() {
      return {
        adminData: {},
        loading: false
      }
    },
    methods: {
      doLogin() {
        this.showLoading()
        this.$http.post('http://185.94.99.69:8080/api/users/admin/login', this.adminData)
          .then(response => {
            this.disableLoading()
            localStorage.setItem('jwt', response.data.data.jwt);
            localStorage.setItem('user', JSON.stringify(response.data.data));

            if (localStorage.getItem('jwt') != null) {

              this.$router.push({path: '/'});
            }


          })
          .catch(error => {
            console.log(error);
          })
      },

      disableLoading () {
        this.loading = false
      },
      showLoading () {
        this.loading = true
      }


    },


  }
</script>

