import axios from 'axios'

const API_URL = 'http://185.94.99.69:8080/api/lesson/text/'

export default class TextApi{


  create (text) {
    return axios.post(API_URL,
      text,
      {
        headers: { 'Authorization': 'Bearer ' + localStorage.getItem('jwt') }
      })
  }

  getText (id) {
    return axios.get(API_URL + id, {
      headers: { 'Authorization': 'Bearer ' + localStorage.getItem('jwt') }
    })
  }

  updateText (id, text) {
    return axios.put(API_URL + id, text, {
      headers: { 'Authorization': 'Bearer ' + localStorage.getItem('jwt') }
    })

  }

  getTexts () {

    return axios.get(API_URL,
      {
        headers: { 'Authorization': 'Bearer ' + localStorage.getItem('jwt') }
      }
    )
  }

  deleteText (id) {
    return axios.delete(API_URL + id,
      {
        headers: { 'Authorization': 'Bearer ' + localStorage.getItem('jwt') }
      }
    )
  }


  getTextsByPaginate (page,limit) {

    return axios.get(API_URL + '?page=' + page + '&limit=' + limit,
      {
        headers: { 'Authorization': 'Bearer ' + localStorage.getItem('jwt') }
      }
    );
  }


  getTextsByPaginateAndFilter (page,limit,lsnId) {

    return axios.get(API_URL + '?page=' + page + '&limit=' + limit + '&lsnId=' + lsnId ,
      {
        headers: { 'Authorization': 'Bearer ' + localStorage.getItem('jwt') }
      }
    );
  }




}
