<template>
  <div>
    <breadcrumb v-bind:title="title"></breadcrumb>


    <div v-bind:class="[{loaderShow:loading ,loaderHidden:!loading }]">
      <Spinner fadeIn="quarter" class="spinnerClass" name="three-bounce" color="#57abfc"/>
    </div>

    <div  v-bind:class="[{loaderShow:!loading ,loaderHidden:loading }]">


      <div class="row">
        <div class="col-sm-12">
          <table class="table table-bordered">
            <tr>
              <th>نام درس</th>
              <th>id</th>
              <th>مدت زمان تست</th>
              <th>ترتیب</th>
              <th>سطح مربوطه</th>
              <th>کاور عکس</th>
              <th>عملیات</th>
            </tr>
            <tbody>
            <tr v-for="lesson in lessons">
              <td>{{lesson.title}}</td>
              <td>{{lesson._id}}</td>
              <td>{{lesson.deadline}}</td>
              <td>{{lesson.order}}</td>
              <td>{{lesson.level.title}}</td>
              <td>
                <img class="thumb" v-bind:src="lesson.avatarUrl">
              </td>
              <td>
                <div class="op-icon">
                  <router-link :to="{name:'EditLesson',params:{lessonid:lesson._id}}"><i class="fa fa-edit"></i>
                  </router-link>
                  <a @click.prevent="deleteLesson(lesson._id)"><i class="fa fa-remove deleteIcon"></i></a>
                </div>
              </td>
            </tr>
            </tbody>
          </table>
        </div>
      </div>

      <div class="row">
        <div class="col-sm-6">

          <template>
            <paginate
              :page-count="totalPage"
              :page-range="3"
              :margin-pages="2"
              :click-handler="clickCallback"
              :prev-text="'Prev'"
              :next-text="'Next'"
              :container-class="'pagination'"
              :page-class="'page-item'">
            </paginate>
          </template>

        </div>
      </div>


    </div>




  </div>
</template>

<script>
  import Breadcrumb from '../../components/Breadcrumb'
  import Notify from '../../services/notifiction/Notification'
  import LessonApi from '../../services/api/lesson/LessonApi'
  const lessonApi = new LessonApi()

  export default {
    name: 'ListLessons',
    components: { Breadcrumb },
    data () {
      return {
        title: 'لیست درس ها',
        lessons: {},
        pageStatus: 'list',
        loading:false,
        page: 1,
        limit: 5,
        totalPage: 1
      }
    },
    methods: {

      clickCallback (pageNum) {
        this.page = pageNum
        this.lessons = {}
        this.getLessons()
      },

      getLessons () {
        this.showLoading;
        lessonApi.getLessonsByPaginate(this.page, this.limit).then((response) => {
        this.disableLoading;
          if (response.data.status_code == 200) {
            this.lessons = response.data.data
            this.totalPage = response.data.totalPage
          }

        })


      },

      deleteLesson (id) {

        lessonApi.deleteLesson(id).then((response) => {

          if (response.data.status_code == 200) {

            Notify.success(response.data.message)
            this.getLessons()

          }

        })

      },

    },
    computed:{

      disableLoading(){
        this.loading = false;
      },
      showLoading(){
        this.loading = true;
      }
    },

    created () {
      this.getLessons()
    }
  }
</script>
