<template>
  <div>
    <breadcrumb v-bind:title="title" v-bind:pageStatus="pageStatus"></breadcrumb>

    <div class="row">
      <div class="col-sm-6">

        <form>
          <div class="form-group">
            <label>عنوان</label>
            <input type="text" name="title" class="form-control" v-model="type.title"/>
            <span class="input_alert">{{showValidationTitle('title')}}</span>
          </div>

          <button v-if="pageStatus === 'add' " class="btn btn-success" @click.prevent="storeType">افزودن</button>
        </form>


      </div>
    </div>


    <div v-bind:class="[{loaderShow:loading ,loaderHidden:!loading }]">
      <Spinner fadeIn="quarter" class="spinnerClass" name="three-bounce" color="#57abfc"/>
    </div>


  </div>
</template>

<script>

  import Breadcrumb from '../../components/Breadcrumb'
  import Notify from '../../services/notifiction/Notification'
  import ValidationErrors from '../../ValidationErrors'
  import TypeApi from '../../services/api/type/TypeApi'

  const typeApi = new TypeApi()

  export default {
    name: 'AddType',
    components: { Breadcrumb },
    data () {
      return {
        title: ' تایپ',
        type: {},
        pageStatus: 'add',
        loading: false,
        ValidationErrors: new ValidationErrors(),
      }
    },
    methods: {

      storeType () {


          this.showLoading

        typeApi.create(this.type).then((response) => {
            this.disableLoading

            this.type = {}

            switch (response.data.status_code) {

              case 201:
                Notify.success(response.data.message)
                break

              case 422://validation error
                this.ValidationErrors.store(response.data.errors)
                Notify.error(response.data.message)
                break

              case 500:
                Notify.error('internal server error')

            }
          })
      },
      showValidationTitle (item) {
        return this.ValidationErrors.errorTitle(item)
      },

    },
    computed: {

      disableLoading () {
        this.loading = false
      },
      showLoading () {
        this.loading = true
      }

    },
  }
</script>

