<template>
  <div>
    <breadcrumb v-bind:title="title"></breadcrumb>



    <div class="row" v-bind:class="[{loaderShow:!loading ,loaderHidden:loading }]">
      <div class="col-sm-12">
        <table class="table table-bordered">
          <tr>
            <th>نام مدیر</th>
            <th>نام کاربری</th>
            <th>عملیات</th>
          </tr>
          <tbody>
          <tr v-for="admin in admins">
            <td>{{admin.name}}</td>
            <td>{{admin.username}}</td>
            <td>
              <div class="op-icon">
                <router-link :to="{name:'EditAdmin',params:{adminid:admin._id}}"><i class="fa fa-edit"></i></router-link>
                <a @click.prevent="deleteAdmin(admin._id)"><i class="fa fa-remove deleteIcon"></i></a>
              </div>
            </td>
          </tr>
          </tbody>
        </table>
      </div>
    </div>


    <div v-bind:class="[{loaderShow:loading ,loaderHidden:!loading }]">
      <Spinner fadeIn="quarter" class="spinnerClass" name="three-bounce" color="#57abfc"/>
    </div>




  </div>
</template>

<script>
  import Breadcrumb from '../../components/Breadcrumb'
  import Notify from '../../services/notifiction/Notification'
  import AdminApi from '../../services/api/admin/AdminApi'
  const adminApi = new AdminApi()


  export default {
    name: 'ListAdmins',
    components: { Breadcrumb },
    data () {
      return {
        title: ' لیست مدیر ها',
        admins: {},
        loading: false,
        pageStatus: 'list'
      }
    },
    methods: {

      disableLoading(){
        this.loading = false;
      },
      showLoading(){
        this.loading = true;
      },

      getAdmins () {

        this.showLoading();
        adminApi.getAdmins().then((response) => {

          this.admins = response.data.data
          this.disableLoading();

        })
      },

      deleteAdmin (id) {

        this.showLoading();

        adminApi.deleteAdmin(id).then((response) => {
          this.disableLoading()
          switch (response.data.status_code) {
            case 200:
              Notify.success(response.data.message)
              this.getAdmins();
              break

            case 422://validation error
              Notify.error(response.data.message)
              break

            case 500:
              Notify.error("internal server error")

          }

        })

      },
    },
    computed:{


    },

    created () {
      this.getAdmins()
    }
  }
</script>
