<template>
  <div>
    <breadcrumb v-bind:title="title" v-bind:pageStatus="pageStatus"></breadcrumb>
    <form style="width: 50%">
      <div class="form-group">
        <label>تیتر نوتیفیکشن</label>
        <input type="text" name="title" class="form-control" v-model="notification.title"/>

        <span class="input_alert">{{showValidationTitle('title')}}</span>
      </div>

      <div class="form-group">
        <label>توضیحات نوتیفیکیشن</label>

        <input type="text" name="description" class="form-control" v-model="notification.text"/>
        <span class="input_alert">{{showValidationTitle('text')}}</span>
      </div>


      <div class="form-group">
        <label>لینک نوتیفیکیشن</label>

        <input type="text" name="description" class="form-control" v-model="notification.link"/>
        <span class="input_alert">{{showValidationTitle('link')}}</span>
      </div>




      <div class="form-group">
        <label>آپلود عکس </label>
        <input type="file" id="file" ref="myFiles" class="custom-file-input"
               @change="previewFiles">
      </div>
      <span class="input_alert">{{showValidationTitle('file')}}</span>




      <button v-if="pageStatus === 'add' " class="btn btn-success" @click.prevent="storeNotification">افزودن</button>
      <button v-else class="btn btn-success" @click.prevent="updateNotification(notification._id)">ویرایش</button>
    </form>


    <div class="row" style="margin-top: 20px;" v-bind:class="[{loaderShow:showProgressBar ,loaderHidden:!showProgressBar }]">
      <div class="col-sm-12">

        <progress-bar v-bind:val="getPercentage" max="100" :text="uploadPercentageWithPercent"></progress-bar>

      </div>
    </div>



  </div>
</template>

<script>
  import Breadcrumb from '../../components/Breadcrumb'
  import Notify from '../../services/notifiction/Notification'
  import ValidationErrors from '../../ValidationErrors'
  import NotificationApi from '../../services/api/notification/NotificationApi'

  const notificationApi = new NotificationApi()

  export default {
    name: 'AddNotification',
    components: { Breadcrumb },
    data () {
      return {
        title: ' نوتیفیکشن',
        notification: {},
        showProgressBar:false,
        files: {},
        pageStatus: 'add',
        uploadPercentage: 0,
        ValidationErrors: new ValidationErrors(),
      }
    },
    methods: {
      storeNotification () {

        this.showingProgressBar();

        var formData = new FormData()

        formData.append('title', this.notification.title)
        formData.append('link', this.notification.link)
        formData.append('text', this.notification.text)
        formData.append('file', this.files)


        this.$http.post('http://185.94.99.69:8080/api/notification/', formData, {

          headers: { 'Authorization': 'Bearer ' + localStorage.getItem('jwt'), 'content-type': 'multipart/form-data' },
          onUploadProgress: function (progressEvent) {
            this.uploadPercentage = parseInt(Math.round((progressEvent.loaded * 100) / progressEvent.total))
          }.bind(this)

        }).then((response) => {
          this.hiddenProgressBar()
          this.resetPercentage()

          switch (response.data.status_code) {

            case 201:
              Notify.success(response.data.message)
              this.notification = {}
              break

            case 200:
              Notify.success(response.data.message)
              this.notification = {}
              break

            case 422://validation error
              this.ValidationErrors.store(response.data.errors)
              Notify.error(response.data.message)
              break

            case 500:
              Notify.error(response.data.message)
              break

          }

        })
      },

      getNotification (id) {

        notificationApi.getNotification(id).then((response) => {

          this.notification = response.data.data

        });

      },


      updateNotification(id) {

        this.showingProgressBar();
        var formData = new FormData()
        formData.append('title', this.notification.title)
        formData.append('link', this.notification.link)
        formData.append('text', this.notification.text)
        formData.append('file', this.files)

        this.$http.put('http://185.94.99.69:8080/api/notification/'+ id, formData, {

          headers: { 'Authorization': 'Bearer ' + localStorage.getItem('jwt'), 'content-type': 'multipart/form-data' },
          onUploadProgress: function (progressEvent) {
            this.uploadPercentage = parseInt(Math.round((progressEvent.loaded * 100) / progressEvent.total))
          }.bind(this)

        }).then((response) => {

          this.hiddenProgressBar()
          switch (response.data.status_code) {

            case 201:
              Notify.success(response.data.message)
              this.notification = {}
              break

            case 200:
              Notify.success(response.data.message)
              this.notification = {}
              break

            case 422://validation error
              this.ValidationErrors.store(response.data.errors)
              Notify.error(response.data.message)
              break

            case 500:
              Notify.error(response.data.message)
              break

          }
          this.resetPercentage();


        })



      },

      previewFiles () {
        this.files = this.$refs.myFiles.files[0]
      },

      hiddenProgressBar () {
        this.showProgressBar = false
      },
      showingProgressBar () {
        this.showProgressBar = true
      },

      showValidationTitle (item) {
        return this.ValidationErrors.errorTitle(item)
      },
      resetPercentage(){
        this.uploadPercentage = 0;
      }
    },
    computed: {

      uploadPercentageWithPercent(){

        return this.uploadPercentage.toString().concat('%');
      },
      getPercentage(){
        return this.uploadPercentage;
      },



    },
    created () {

      if (this.$route.params.notificationid) {
        this.pageStatus = 'edit'
        this.getNotification(this.$route.params.notificationid)
      }

    }
  }
</script>

