<template>
  <div>
    <breadcrumb v-bind:title="title"></breadcrumb>


    <div v-bind:class="[{loaderShow:loading ,loaderHidden:!loading }]">
      <Spinner fadeIn="quarter" class="spinnerClass" name="three-bounce" color="#57abfc"/>
    </div>


    <div v-bind:class="[{loaderShow:!loading ,loaderHidden:loading }]">


      <div class="row">
        <div class="col-sm-3">
          <div class="form-group">
            <label>فیلتر بر اساس درس</label>
            <v-select v-model="lessonIdFilter" :options="lessons" return-object></v-select>
          </div>
        </div>
      </div>


      <div class="row">
        <div class="col-sm-12">
          <table class="table table-bordered">
            <tr>
              <th>نام ویدیو</th>
              <th>نوع ویدیو</th>
              <th>درس مربطه</th>
              <th>کاور</th>
              <th>ترتیب</th>
              <th>عملیات</th>
            </tr>
            <tbody>
            <tr v-for="video in videos">
              <td>{{video.title}}</td>
              <td>{{video.type.title}}</td>
              <td>{{video.lesson.title}}</td>
              <td>
                <img class="thumb" v-bind:src="video.thumbUrl">
              </td>
              <td>{{video.order}}</td>
              <td>
                <div class="op-icon">
                  <router-link :to="{name:'EditVideo',params:{videoid:video._id}}"><i class="fa fa-edit"></i>
                  </router-link>
                  <a @click.prevent="deleteVideo(video._id)"><i class="fa fa-remove deleteIcon"></i></a>
                </div>
              </td>
            </tr>
            </tbody>
          </table>
        </div>
      </div>


      <div class="row">
        <div class="col-sm-6">

          <template>
            <paginate
              :page-count="totalPage"
              :page-range="3"
              :margin-pages="2"
              :click-handler="clickCallback"
              :prev-text="'Prev'"
              :next-text="'Next'"
              :container-class="'pagination'"
              :page-class="'page-item'">
            </paginate>
          </template>

        </div>
      </div>
    </div>


  </div>
</template>

<script>
  import Breadcrumb from '../../components/Breadcrumb'
  import Notify from '../../services/notifiction/Notification'
  import VideoApi from '../../services/api/video/VideoApi'
  import LessonApi from '../../services/api/lesson/LessonApi'

  const videoApi = new VideoApi()
  const lessonApi = new LessonApi()

  export default {
    name: 'ListVideos',
    components: { Breadcrumb },
    data () {
      return {
        title: 'لیست ویدیو ها',
        videos: {},
        lessons: [],
        pageStatus: 'list',
        loading: false,
        page: 1,
        limit: 5,
        totalPage: 1,
        lessonIdFilter: ''
      }
    },
    methods: {

      clickCallback (pageNum) {
        this.page = pageNum
        this.videos = {}
        this.getVideos()
      },

      getVideos () {
        this.showLoading

        videoApi.getVideosByPaginate(this.page, this.limit).then((response) => {
          this.disableLoading
          if (response.data.status_code == 200) {
            this.videos = response.data.data
            this.totalPage = response.data.totalPage
          }

        })
      },

      getVideosByPaginateAndFilter (lsnid) {
        this.showLoading

        videoApi.getVideosByPaginateAndFilter(this.page, this.limit,lsnid).then((response) => {
          this.disableLoading
          if (response.data.status_code == 200) {
            this.videos = response.data.data
            this.totalPage = response.data.totalPage
          }

        })
      },

      deleteVideo (id) {

        videoApi.deleteVideo(id).then((response) => {

          if (response.data.status_code == 200) {

            Notify.success(response.data.message)
            this.getVideos()

          }

        })

      },

      getLessonsSelective () {

        lessonApi.getLessonsSelective().then((response) => {

          this.lessons = response.data.data

        })

      },

    },
    computed: {

      disableLoading () {
        this.loading = false
      },
      showLoading () {
        this.loading = true
      }
    },
    created () {
      this.getVideos()
      this.getLessonsSelective()
    },
    watch: {

      'lessonIdFilter': function () {
        const lsnId =  this.lessonIdFilter;

        if(!lsnId)
        {

          this.getVideos()

        }else{
          this.getVideosByPaginateAndFilter(this.lessonIdFilter.value)

        }



      }
    }

  }
</script>

